.container {
  --columns: 2;
  grid-template-columns: repeat(var(--columns), 1fr);
  column-gap: 20px;
  margin-inline: auto;
  padding-inline: 30px;
  display: grid;
}

@media (width >= 640px) {
  .container {
    margin-inline: auto;
    padding-inline: 34px;
  }
}

@media (width >= 1170px) {
  .container {
    margin-inline: auto;
    padding-inline: 135px;
  }
}

@media (width >= 640px) {
  .container {
    --columns: 6;
    column-gap: 20px;
  }
}

@media (width >= 1170px) {
  .container {
    --columns: 12;
    column-gap: 30px;
  }
}

h1 {
  font-family: Bebas Neue, sans-serif;
  font-size: 58px;
  font-weight: 400;
}

@font-face {
  font-family: Roboto, Arial, Helvetica, sans-serif;
  src: url("Roboto-Regular-webfont.55e67705.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

html, body {
  scroll-behavior: smooth;
  background: #f8f8f8;
}

.page {
  min-width: 320px;
  position: relative;
}

.page__body {
  min-width: 260px;
  margin: 0;
}

.page:has(.page__menu:target) {
  overflow: hidden;
}

.page__menu {
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateX(200%);
}

.page__menu:target {
  opacity: 1;
  pointer-events: all;
  overflow: visible;
  transform: translateX(0);
}

@media (width >= 1170px) {
  .page__menu {
    pointer-events: all;
    opacity: 1;
    position: absolute;
    transform: translateX(0);
  }
}

.main {
  display: grid;
  overflow: hidden;
}

.main__content {
  grid-column: 1 / -1;
  gap: 110px;
  padding: 110px 0;
  display: grid;
}

.header {
  z-index: 0;
  position: relative;
}

@media (width >= 640px) {
  .header__backround-yelow-color {
    z-index: -1;
    box-sizing: border-box;
    background-image: linear-gradient(165deg, #ffec7a 50%, #0000 50%);
    width: 80%;
    height: 55vw;
    display: flex;
    position: absolute;
  }
}

.header__content {
  flex-direction: column;
  grid-column: 1 / -1;
  justify-content: space-between;
  display: flex;
}

.header__bottom {
  position: relative;
}

.header__bottom:after {
  content: "More about  POTR POTS";
  color: #03839e;
  font-family: Manrope, sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
  position: absolute;
  bottom: 115px;
  right: -80px;
  transform: rotate(90deg);
}

@media (width >= 640px) {
  .header__bottom:after {
    right: 84%;
    right: unset;
    bottom: 75px;
    left: -60px;
  }
}

.header__bottom:before {
  content: url("arrow-blue.708295a1.png");
  position: absolute;
  bottom: 0;
  right: -20px;
}

@media (width >= 640px) {
  .header__bottom:before {
    right: unset;
    bottom: -40px;
    left: 2px;
  }

  .header__bottom {
    --columns: 2;
    grid-template-columns: repeat(var(--columns), 1fr);
    column-gap: 20px;
    margin-top: 30px;
    display: grid;
  }
}

@media (width >= 640px) and (width >= 640px) {
  .header__bottom {
    --columns: 6;
    column-gap: 20px;
  }
}

@media (width >= 640px) and (width >= 1170px) {
  .header__bottom {
    --columns: 12;
    column-gap: 30px;
  }
}

.header__bg-img-container {
  grid-column: 1 / -1;
  height: 100vw;
  margin-bottom: 25px;
}

@media (width >= 640px) {
  .header__bg-img-container {
    z-index: 0;
    order: 2;
    grid-column: 5 / span 2;
    height: 40vw;
    position: relative;
  }
}

@media (width >= 1170px) {
  .header__bg-img-container {
    z-index: 0;
    order: 2;
    grid-column: 8 / span 5;
    height: 35vw;
    margin: 10% 0 0;
    position: relative;
  }
}

.header__bg-image {
  width: 100vw;
  display: flex;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

@media (width >= 640px) {
