  -webkit-text-stroke: 1.07px #03839e;
}

.article-header__title__group-1--no-spases {
  word-spacing: -.4ch;
}

.article-header__title__group-2 {
  white-space: nowrap;
  -webkit-text-fill-color: #03839e;
}

