}

@media (width >= 640px) {
  .article-header__title {
    letter-spacing: .15em;
    margin: 0;
    font-size: 64px;
    line-height: 64px;
  }
}

@media (width >= 1170px) {
  .article-header__title {
    grid-column: 2 / 5 span;
    margin: 10% 0 0;
  }
}

.article-header__price {
  color: #333;
  grid-column: 1 / -1;
  margin: 0;
  font-family: Manrope, sans-serif;
  font-size: 26px;
  font-weight: 700;
  line-height: 35.52px;
  display: grid;
}

@media (width >= 640px) {
  .article-header__price {
    grid-column: 3 / 4;
