  }
}

.article-header__description {
  color: #4f4f4f;
  grid-column: 1 / -1;
  margin: 0 0 20px;
  font-family: Manrope, sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
  display: grid;
}

@media (width >= 640px) {
  .article-header__description {
    grid-column: 3 / 5;
    position: relative;
    top: -50px;
  }
}

@media (width >= 1170px) {
  .article-header__description {
    grid-column: 5 / 2 span;
    height: min-content;
    margin: 0 0 50px;
    top: 0;
  }
